.midImg {
    .midImg__inner {
        img {
            margin: 0 auto;
        }
    }
}
.midImg {
    padding-bottom: 4em;
    .midImg__inner {
        position: relative;
        .midImg__img {
            display: flex;
            img:first-child {
                position: absolute;
                z-index: 1;
                filter: blur(6px);
            }
            img:nth-child(2) {
                z-index: 5;
            }
            img:last-child {
                position: absolute;
                z-index: 1;
                right: 0;
                filter: blur(6px);
            }
        }
    }
}
@media (max-width: 768px) {
    .midImg {
        .midImg__inner {
            .midImg__img {
                img:first-child, img:last-child {
                    display: none;
                }
            }
        }

    }
}
