.footer {
  padding: 40px 0;

  background-color: #F9F9F9;
  .footer__inner {
    .footer__links.mobile {
      display: none;
      column-gap: 2em;
      flex-direction: column;
    }
    .footer__header {
      display: flex;
      justify-content: space-between;
      .nav {
        background-color: transparent;
        display: flex;
        column-gap: 3em;
        font-weight: 600;
      }
    }
    .footer__content {
      display: flex;
      justify-content: space-between;
      
      .socialBtns {
        display: flex;
        
        margin-top: 1em;
        margin-bottom: 1em;
        column-gap: 1em;
        button.btn {
          width: 30px;
          aspect-ratio: 1;
          background-color: #825171;
          border-radius: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          svg {
            flex-shrink: 0;
          }
        }
      }
      .footer__info {
        h4.title {
          color: #790000;
        }
        .phones {
          display: flex;
          flex-direction: column;
          font-weight: 700;
          row-gap: .5em;
        }
        .sites {
          margin-top: 1em;
          margin-bottom: 1em;
          a {
            text-decoration: underline;
          }
        }
        .email {
          span {
            font-weight: 600;
          }
        }
      }
      .footer__column:first-child {
        max-width: 50%;
      }
      .footer__links {
        display: flex;
        column-gap: 2em;
        padding-top: 1em;
      }
    }
  }

}

@media (max-width: 768px) {
  .footer {
    .footer__inner {
      text-align: center;
      .footer__header {
        flex-direction: column;
        img {
          margin: 0 auto;
          margin-bottom: 1em;
        }
        .nav {
          column-gap: 1em;
          justify-content: space-between;
        }
      }
      .footer__content {
        flex-direction: column;
        .footer__links {
          display: none;
        }
        .socialBtns {
          justify-content: center;
        }
      }
      .footer__links.mobile {
        display: flex;
        margin-top: 2em;
      }
    }
  }
  .footer .footer__inner .footer__content .footer__column:first-child {
    max-width: 100%;
  }
}