

.miloga {
    background-color: #F9F9F9;
    .miloga__inner {
        h2.section__title {
            color: #18161D;
        }
        .miloga__context {
            .miloga__context {
                .miloga__text {
                    display: flex;
                    justify-content: space-between;
                    text-align: justify;
                }

            }
        }
        .miloga__pics {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 1em;
            img {
                border: 3px solid #FFFFFF;
                box-shadow: 1px 1px 15px 0px #696969;
                width: 590px;
                height: 340px;
                object-fit: cover;
            }
        }
    }
}

@media (max-width: 768px) { 
    .miloga {
        .miloga__inner {
            .miloga__pics {
                grid-template-columns: 1fr;
            }
        }
    }
}