.orchestra {
    margin-top: 3.5em;
    .orchestra__main {
        display: flex;
        flex-direction: row-reverse;
        column-gap: 2em;
        font-size: 18px;
        line-height: 1.43;
        text-align: justify;
        p {
            margin: 0;
            margin-bottom: 1em;
        }
        .orchestra__pic {
            width: 50%;
            flex-shrink: 0;
            margin: 0 auto;
            img {
                width: 100%;
                border: 3px solid #fff;
                box-shadow: 1px 1px 15px 0px #696969;
            }
        }
    }
}

@media (max-width: 768px) {
    .orchestra {
        .orchestra__main {
            font-size: 14px;
            flex-direction: column-reverse;
            .orchestra__pic {
                width: 100%;
                margin: 0 auto;
                margin-bottom: 1em;
            }
        }
    }
}