.price {
    .desc {
        text-align: justify;
    }
     .price__block {
        background-color: #F9F9F9;
        padding: 1em;
        margin-bottom: 2em;
        ul {
            padding-left: 1em;
        }
        &.special .price__blockContext, &.indPack .price__blockContext{
            display: none;
        }
        .price__header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .toggleBtn {
                background-color: $color-accent-main;
                width: 40px;
                height: 40px;
                border-radius: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-shrink: 0;
            }
        }
        &.active {
            .price__blockContext {
                display: block;
            }
            .price__header {
                .toggleBtn {
                    img {
                        transform: rotate(180deg);
                    }
                }
            }
        }
        .subTitleBlock {
            font-size: 24px;
            font-family: 'Academy';
            text-transform: uppercase;
        }
        .note {
            color: #707070;
        }
        &.price__list {
            overflow-x: scroll;
            table {
                font-weight: 600;
                .title {
                    color: #790000;
                }
            }
        }
        &.indPack {
            table {
                font-weight: 600;
            }
        }
     }
}