.picLine {
    display: flex;
    column-gap: 1em;
    img {
        border: 3px solid #fff;
        box-shadow: 1px 1px 15px 0px #696969;
    }
}
.textBlock, .picTextBlock {
    text-align: justify;
}
.picTextBlock {
    display: flex;
    column-gap: 2.5em;
}
section.concertHeader {
    margin-top: 2.7em;
    .concertHeader__inner {
        .descLine {
            display: flex;
            column-gap: 1.3em;
            margin-top: 2em;
            h2 {
                font-family: "Academy";
                font-weight: 400;
                font-size: 24px;
                color: #790000;
                text-transform: uppercase;
                max-width: 50%;
            }
        }
    }
}
section.concertBlock.gala {
    .concertBlock__inner {
        padding: 40px;
        background-color: #F9F9F9;
        h2.section__title {
            color: #825171;
            font-size: 32px;
        }
        img {
            border: 3px solid #fff;
            box-shadow: 1px 1px 15px 0px #696969;
        }

    }
}
section.perfomance {
    .perfomance__inner {
        h2.section__title {
            color: #825171;
            font-size: 32px;
        }
        .perfomance__list {
            //display: grid;
            //grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

            display: flex;
            justify-content: space-between;

            border-bottom: 1px solid #D9D9D9;
            padding-bottom: 3em;
            
            padding-left: 0;
            gap: 1em;
            list-style: none;
            li {
                text-align: center;
                font-weight: 600;
                text-decoration: underline;
                color: #790000;
                a {
                    color: #790000;
                }
                img {
                    border: 3px solid #fff;
                    box-shadow: 1px 1px 15px 0px #696969;
                    width: 100%;
                    margin-bottom: 1em;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    section.concertHeader .concertHeader__inner .descLine h2 {
        font-size: 18px;
    }
    .picLine {
        flex-direction: column;
        align-items: center;
    }
    .descLine {
        flex-direction: column;
        h2 {
            max-width: 100%;
        }
    }
    .picTextBlock {
        flex-direction: column;
    }
    section.concertHeader .concertHeader__inner .descLine h2 {
        max-width: 100%;
    }
    section.concertBlock.gala {
        .concertBlock__inner {
            padding: 1em;
            h2.section__title {
                font-size: 24px;
            }
        }
    }
    section.concertBlock.location  {
        .picTextBlock {
            flex-direction: column-reverse;
        }
    }
    section.perfomance .perfomance__inner .perfomance__list {
        flex-direction: column;
    }
}