.events {
    margin-top: 4em;
    background-color: #f9f9f9;
    .events__inner {
        .section__title {
            font-size: 32px;
            max-width: 800px;
            line-height: 1.3;
            color: #18161D;

        }
        .events__list {
            margin-top: 1em;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            padding-left: 0;
            gap: 1em;
            .btn {
                color: #18161D;
                padding: 2.8em 2em;
                border: 1px solid #D9D9D9;
                text-align: center;
                cursor: pointer;
                font-weight: 600;
                font-size: 18px;
                line-height: 1.5;
                &:nth-child(2), &:nth-child(4), &:nth-child(5), &:nth-child(7) {
                    color: $color-accent-main;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .events {
        margin-top: 0em;
        .events__inner {
            h2.section__title {
                font-size: 18px;
            }
            .events__list {
                grid-template-columns: 1fr;
                .btn {
                    &:nth-child(2), &:nth-child(4), &:nth-child(5), &:nth-child(7) {
                        color: #18161D;
                    }
                    &:nth-child(even) {
                        color: $color-accent-main;
                    }
                }
            }
        }
    }
    
}