.modalOverlay {
    display: none;
    position: fixed;
    z-index: 10;
    background-color: rgba($color: #000000, $alpha: 0.2);
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    &.active {
        display: block;
    }
}
.modalWindow {
    display: none;
    position: fixed;
    left: 50vw;
    top: 50vh;

    transform: translate(-50%, -50%);

    background-color: #fff;

    padding: 2em;
    border-radius: 12px;
    z-index: 20;
    width: 700px;
    //height: 100%;
    max-height: 600px;
    overflow-y: auto;
    &.active {
        display: block;
    }
    .modalWindow__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2.section__title {
            font-size: 24px;
            margin-bottom: 0;
        }
        .closeBtn {
            flex-shrink: 0;

            border-radius: 100%;
            width: 40px;
            height: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #fff;
            background-color: $color-accent-main;
            cursor: pointer;
        }
    }
    .modalWindow__text {
        margin-top: 1em;
        height: 100%;
        overflow-y: auto;
    }
}
    
        
@media (max-width: 768px) {
    .modalWindow {
        width: 500px;
        .modalWindow__header {
            h2.section__title {
                font-size: 18px;
            }
        }
    }
}

@media (max-width: 576px) {
    .modalWindow {
        top: 0;
        transform: translateX(-50%);
        width: 100%;
        max-height: 100%;
        .modalWindow__text {
            height: 100vh;
        }
    }
}