section.tornamentHeader {
    .tornamentHeaderBlock {
        display: flex;
        column-gap: 1em;
        justify-content: space-between;
        background-color: #F9F9F9;
        padding: 40px;
        h2.section__title {
            font-size: 32px;
        }
        .tornamentHeaderBlock__content {
            width: 50%;
        }
    }
}
section.tornamentIntro {
    background-color: #F9F9F9;
    .tornamentIntro__inner {
        h2 {
            font-size: 32px;
        }
        p {
            text-align: justify;
        }
        .tornamentIntro__pics {
            display: flex;
            justify-content: space-between;
            img {
                height: revert-layer;
                object-fit: cover;
                border: 3px solid #fff;
                box-shadow: 1px 1px 15px 0px #696969;
                object-position: top;
            }
            
        }
    }
}
section.tornamentPlace {
    .tornamentPlace__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 2.5em;
        h2 {
            font-size: 32px;
        }
        img {
            border: 3px solid #fff;
            box-shadow: 1px 1px 15px 0px #696969;
        }

    }
}
section.tornamentCats {
    .tornamentCats__inner {
        display: flex;
        justify-content: space-between;
        column-gap: 1em;
        h2 {
            font-size: 32px;
        }
        .column {
            background-color: #F9F9F9;
            padding: 2em;
            &:first-child {
                font-weight: 600;
            }
            ul {
                li{
                    margin-bottom: 1em;
                    &::marker {
                        color: $color-accent-main;
                    }
                }
            }
        }
    }
}
section.tornamentJury {
    .tornamentJury__inner {
        h2.section__title {
            font-size: 32px;
        }
        img {
            height: revert-layer;
            object-fit: cover;
        }
        p {
            text-align: justify;
            a {
                font-weight: 600;
                text-decoration: underline;
            }
        }
    }
}
@media (max-width: 768px) {
    section.perfomance .perfomance__inner .perfomance__list, section.tornamentHeader .tornamentHeaderBlock {
        flex-direction: column;
    }
    section.tornamentHeader .tornamentHeaderBlock{
        h2.section__title {
            font-size: 18px;
        }
    }
    section.tornamentHeader .tornamentHeaderBlock .tornamentHeaderBlock__content {
        width: 100%;
        text-align: center;
    }
    .tornamentHeaderBlock__pic {
        img {
            margin: 0 auto;
        }
    }
    .tornamentIntro__inner {
        text-align: justify;
    }
    .tornamentIntro__pics {
        
        img {
            margin: 0 auto;
        }
    }
    .tornamentPlace__inner {
        flex-direction: column;
        text-align: center;
    }
    .tornamentCats__inner{
        flex-direction: column;
    }
}