@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Inter";

  font-display: swap;
  src: url("../fonts/Inter-Regular.ttf") format("truetype");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Inter";

  font-display: swap;
  src: url("../fonts/Inter-Medium.ttf") format("truetype");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Inter";

  font-display: swap;
  src: url("../fonts/Inter-SemiBold.ttf") format("truetype");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Academy";

  font-display: swap;
  src: url("../fonts/Academy.woff2") format("woff2"),
      url("../fonts/Academy.woff") format("woff");
}