.regDesc__text {
    text-align: justify;
}
form.regForm__main {
    background-color: #f9f9f9;
    padding: 40px;
    h2.section__title {
        font-size: 24px;
    }
    input[type="checkbox"] {
        appearance: none;
        width: 20px;
        height: 20px;
        border: 2px solid #18161D;
        background-image: url(../img/svg/mark.svg);
        background-repeat: no-repeat;
        background-position: center center;
        &:checked {
            background-color:#790000;
            border-color: #790000;
        }
    }
    .regForm__block {
        padding-top: 2em;
        padding-bottom: 2em;
        border-bottom: 1px solid #D9D9D9;
    }
    .note {
        margin-bottom: 1em;
        p {
            margin: 0;
        }
    }
    input[type="radio"] {
        appearance: none;
        width: 20px;
        height: 20px;
        border: 2px solid #18161D;
        border-radius: 100%;
        position: relative;
        &:checked {
            border-color: #790000;
            &::after {
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                background-color:#790000;
                left: 3px;
                top: 3px;
                border-radius: 100%;
            }
        }

    }
    .redMark {
        color: #D93025;
    }
    input[type="text"],  input[type="email"], input[type="tel"]{
        background-color: #FFFFFF;
        border: 1px solid #D9D9D9;
        color: #18161D;
        padding: 12px 20px;
        &::placeholder {
            color: #707070;
        }
    }
    textarea {
        border: 1px solid #D9D9D9;
    }
    .row {
        margin-bottom: 1em;
        display: flex;
        align-items: center;
        column-gap: 1em;
        #tkCount {
            width: 60px;
            text-align: center;
        }
        input {
            flex-shrink: 0;
        }
        &.otherInputRow .el{
            display: flex;
            column-gap: 1em;
            align-items: center;

        }
        &.otherInputRow input[type="text"] {
            width: 400px;
        }
        a {
            text-decoration: underline;
        }

    }
    textarea {
        padding: 1em;
        &::placeholder {
            color: #707070;
        }
    }
    .regForm__main {
        display: flex;
        column-gap: 2em;
        .inputEl {
            display: flex;
            flex-direction: column;
            margin-bottom: 1em;
            label {
                margin-bottom: 0.2em;
            }
            &.levelField {
                margin-bottom: 2em;
                .row {
                    column-gap: 1.4em;
                }
                .el {
                    display: flex;
                    align-items: center;
                    column-gap: 1em;
                }
            }
        }
        .row {
            margin-bottom: 0;

        }
    }
    .regForm__block.comment {
        .column {
            display: flex;
            flex-direction: column;
        }
        label {
            margin-bottom: 0.5em;
        }
    }
    button.btn.sendReg {
        background-color: #825171;
        color: #fff;
        padding: 20px;
        font-weight: 600;
        cursor: pointer;
    }
    .regForm__block.footer a{
        text-decoration: underline;
    }
}

@media (max-width: 768px) {
    form.regForm__main {
        padding: 20px;
        .regForm__main {
            flex-direction: column;
            .levelField .row {
                flex-direction: column;
                align-items: start;
                
                .el {
                    display: flex;
                    align-items: center;
                    column-gap: 1em;
                }
            }
        }
        h2.section__title {
            font-size: 18px;
            text-align: left;
        }
        .row.otherInputRow {
            flex-direction: column;
            align-items: start;
            row-gap: .5em;
            input[type="text"] {
                width: 100%;
            }
        }

    }

}