
.nav {
  background-color: #825171;
  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: center;

    padding: 1.25em 0;

    column-gap: 3em;

    list-style: none;

    font-weight: 600;
  }

  &__item {
    //margin-right: 20px;
  }

  &__link {
    color: $color-default-white;

    &.is-active {
      opacity: 0.6;
    }
  }

  &__toggle {
    display: none;
  }
}

@media (max-width: 768px) {
  .nav__list {
    column-gap: 1.2em;
  }
}