.schedule {
    .schedule__header {
        display: flex;
        justify-content: space-between;
        .section__desc {
            max-width: 500px;
        }

    }.schedule__list {

    }
    .schedule__item {
        padding: 1em;
        .item__header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-top: 1em;
            padding-bottom: 1em;
            border-bottom: 1px solid #18161D;
            font-size: 24px;
            font-family: "Academy";
            text-transform: uppercase;
            .toggleBtn {
                background-color: $color-accent-main;
                width: 40px;
                aspect-ratio: 1;
                border-radius: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
        .item__content {
            display: none;
            overflow-x: scroll;
            table tr td:nth-child(2) {
                font-weight: 600;
                .title {
                    color: #790000;
                    display: inline-block;
                    margin-bottom: 1em;
                }
            }
        }
        &.active {
            .item__content {
                display: block;
            }
            .item__header {
                .toggleBtn {
                    img {
                        transform: rotate(180deg);
                    }
                }
            }
        }
    }
    .note {
        color:  #707070;
    }
}
@media (max-width: 768px) {
    .schedule__header {
        flex-direction: column;
    }
}