.about {
    .about__inner {
        display: flex;
        margin-top: 3em;
        justify-content: space-between;
        column-gap: 2em;
        h2.section__title.mobile {
            display: none;
        }
        .about__context {
            //width: 50%;
            h2.section__title {
                display: block;
            }
            font-size: 18px;
            p {
                font-style: italic;
                margin-bottom: 2em;
                text-align: justify;
            }
        }
        .about__pic {
            flex-shrink: 0;
            img {
                max-width: 590px;
                max-height: 500px;
                height: revert-layer;

                object-fit: cover;
                border: 3px solid #FFFFFF;
                box-shadow: 1px 1px 15px 0px #696969;
            }
        }

    }
}

@media (max-width: 768px) {
    .about {
        .about__inner {
            flex-direction: column-reverse;
            margin-top: 0.8em;
            
            h2.section__title.mobile {
                display: block;
            }
            img {
                width: 100%;
            }
            .about__context {
                font-size: 14px;
                h2.section__title {
                    display: none;
                }
            }
            .about__pic {
                img {
                    height: unset;
                }
                
            }
        }
    }
}