.program__item {
    background-color: #F9F9F9;
    padding-bottom: 1em;
    margin-bottom: 1em;
    .item__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 24px;
        font-family: "Academy";
        text-transform: uppercase;
        .toggleBtn {
            background-color: $color-accent-main;
            width: 40px;
            aspect-ratio: 1;
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                transition: transform .3s ease-in;
            }
        }
        padding: 1em;
    }
    .item__content {
        display: none;
        overflow-x: scroll;
        li {
            &::marker {
                color: $color-accent-main;
            }
            .time {
                font-weight: 600;
                color:  #790000;
            }
        }
    }
    &.active {
        .item__content {
            display: block;
        }
        .item__header {
            .toggleBtn {
                img {
                    transform: rotate(180deg);
                }
            }
        }
    }
}