.locations {
    background-color: #F9F9F9;
    .locations__inner {
        padding-top: 3.9em;
        h2.section__title {
            color: #18161D;
        }
        .locations__main {
            padding: 0em 1.9em;
            background-color: #fff;
            //box-shadow: 1px 1px 15px 0px #696969;
            .locations__item {
                display: flex;
                border-bottom: 1px solid #D9D9D9;
                align-items: center;
                justify-content: space-between;
                column-gap: 2em;
                padding-top: 1.7em;
                padding-bottom: 2em;
                line-height: 1.78;
                text-align: justify;
                &:nth-child(even) {
                    flex-direction: row-reverse;
                }
                &:last-child {
                    border: none;
                }
                .locations__pic {
                    flex-shrink: 0;
                    width: 30%;
                    box-shadow: 1px 1px 15px 0px #696969;
                    img {
                        border: 3px solid #fff;
                    }
                }
            }
        }

    }
}

@media (max-width: 768px) {
    .locations {
        .locations__inner {
            .locations__main {
                .locations__item {
                    flex-direction: column-reverse;
                    &:nth-child(even) {
                        flex-direction: column-reverse;
                    }
                    .locations__pic {
                        width: 100%;
                        box-shadow: 1px 1px 15px 0px #696969;
                    }
                }
            }
        }
    }
}