.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;

  @include vp-1439 {
    padding: 0 32px;
  }

  @include vp-1023 {
    padding: 0 24px;
  }

  @include vp-767 {
    padding: 0 16px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
  
}

@media (max-width: 576px) {
  .container {
    //padding: 0;
  }
}