// адаптив пишем внутри каждого класса (смотрим container.scss)

// для фикса проблем с vh на iOS в сборке подключен скрипт
// используя vh на проекте задавайте их также как в примере в utils.scss

// для любых transition обязательно указывайте transition-property
// transition: $trans-default ❌ ---> transition: color $trans-default ✅

section {
  padding-top: 2em;
  padding-bottom: 2em;
}

.section__title {
  font-size: 60px;
  margin-top: 0;
  line-height: 1;
  font-family: 'Academy';
  text-transform: uppercase;
  font-weight: 400;
  color: $color-accent-main;
  word-spacing: 10px;
}

table {
  border-collapse: collapse;
  overflow-x: scroll;
  th {
    text-align: left;
    background-color: $color-accent-main;
    color: #fff;
    border-right: 1px solid #D9D9D9;
    padding: 1em;
  }
  td {
    border: 1px solid #D9D9D9;
    padding: 1em;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
  .section__title {
    font-size: 24px;
    text-align: center;
  }
}