.djs {
    margin-top: 4.5em;
    .djs__inner {
        border-bottom: 1px solid #D9D9D9;
        .djs__list {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
            padding-left: 0;
            gap: 1em;
            list-style: none;
            li {
                text-align: center;
                font-weight: 600;
                text-decoration: underline;
                cursor: pointer;
                color: #790000;
                a {
                    color: #790000;
                }
                img {
                    border: 3px solid #fff;
                    box-shadow: 1px 1px 15px 0px #696969;
                    width: 100%;
                    margin-bottom: 1.5em;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .djs {
        .djs__inner {
            .djs__list {
                grid-template-columns: 1fr;
                li {
                    margin-top: 2em;
                    img {
                        width: 285px;
                        margin: 0 auto;
                        margin-bottom: 1em;
                    }
                }
            }
            
        }
    }
}